
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  a {
    text-decoration: none;
    color: inherit;
  }
  li {
    list-style: none;
  }
  body {
    line-height: 1;
    color: #333;
}

.header {
    height: 100px;
    background-color: rgba(255, 255, 255, 0.8);
}
.header .inner {
    max-width: 1280px;
    width: calc(100% - 30px);
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
}
.header .inner h1 {
    font-size: 35px;
}
.header .inner .nav .gnb {
    display: flex;
    column-gap: 40px;
}
.header .inner .nav .gnb li {
    font-size: 18px;
    font-weight: bold;
}
.header .inner .nav_open {
    position: relative;
    z-index: 9;
    cursor: pointer;
    width: 20px;
}
.header .inner .nav_open img {
    width: 100%;
    /* object-fit: cover; */
}
.all_nav {
    width: 40%;
    height: 100vh;
    position: fixed;
    right: -70%;
    top: 0;
    background-color: rgb(0, 128, 0, 0.99);
    color: #fff;

    transition: 0.3s;
}
.all_nav .gnb {
    padding: 40px 20px;
}
.all_nav .gnb li {
    padding: 20px;
}
.all_nav.on {
    right: 0;
    transition: 0.5s;
}
body.on {
    overflow: hidden;
}
.input_box input {
    border-radius: 20px;
    padding: 5px;
    background-color: white;
    border: 2px solid #ff0000;
}
.input_box {
    width: 200px;
    display: flex;
    flex-direction: column;
    row-gap: 30px;
}
.input_box .text {
    width: 100%;
    border: 2px dotted #000;
    color: #fff;
}